.head {
  position: relative;
  margin-top: 76px;
  overflow: hidden;
  height: 600px;
}
.head .bgimg {
  position: absolute;
  width: 5000px;
  height: 600px;
  top: 0;
  left: 50%;
  margin-left: -2500px;
  background-color: #fafafa;
}
.head .bgimg img {
  position: relative;
  display: block;
  height: 130%;
  margin: 0 auto;
  top: -15%;
}
.head .blkcent {
  box-sizing: border-box;
  padding-left: 110px;
}
.head .blkcent h1 {
  font-size: 56px;
  width: 450px;
  font-weight: bold;
  color: #4d4d4d;
  line-height: 72px;
  margin-top: 120px;
  font-weight: 400;
}
.head .blkcent h2 {
  font-size: 29px;
  font-weight: 400;
  color: #4a4a4a;
  line-height: 41px;
  margin-top: 25px;
}
.head .blkcent p {
  width: 550px;
  font-size: 15px;
  color: #4a4a4a;
  line-height: 25px;
  margin-top: 15px;
}
.head .blkcent button {
  width: 224px;
  height: 60px;
  background: linear-gradient(225deg, #fa2b56 0%, #f91c3d 100%);
  box-shadow: 20px 20px 60px 0px rgba(0, 0, 0, 0.4);
  border-radius: 5px;
  outline: none;
  border: none;
  margin-top: 60px;
  font-size: 22px;
  color: #ffffff;
  transition-duration: 0.3s;
  cursor: pointer;
}
.head .blkcent button:hover {
  opacity: 0.8;
}
.ai {
  padding-top: 150px;
  padding-bottom: 200px;
}
.ai .title {
  font-size: 30px;
  color: #4A4A4A;
  text-align: center;
}
.ai p {
  width: 848px;
  height: 64px;
  font-size: 20px;
  color: #4a4a4a;
  line-height: 32px;
  margin: 0 auto;
  margin-top: 40px;
}
.ai ul {
  margin-top: 86px;
}
.ai ul li {
  float: left;
  width: 16.66%;
  text-align: center;
}
.ai ul li img {
  height: 83px;
  margin: 0 auto;
  display: block;
}
.ai ul li span {
  display: block;
  text-align: center;
  font-size: 20px;
  color: #4A4A4A;
  margin-top: 15px;
}
.apps {
  padding: 100px 0;
}
.apps .dubtitle {
  padding-bottom: 80px;
}
.apps .left {
  position: relative;
  width: 46%;
  height: 362px;
  overflow: hidden;
  float: left;
  margin: 0 2%;
  margin-bottom: 48px;
}
.apps .bg {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.apps .applogo {
  position: relative;
  margin: 40px 0 0 30px;
  width: 100px;
  z-index: 10;
}
.apps p {
  position: relative;
  font-size: 14px;
  color: #ffffff;
  width: 230px;
  margin-left: 40px;
  margin-bottom: 5px;
  line-height: 25px;
  z-index: 10;
}
.apps .donwload {
  position: absolute;
  left: 30px;
  bottom: 50px;
  margin-left: 10px;
}
.apps .donwload a {
  display: inline-block;
  position: relative;
  width: 110px;
  height: 33px;
  margin-right: 20px;
  overflow: hidden;
}
.apps .donwload a:hover .downhov {
  opacity: 1;
}
.apps .donwload a:hover .downdft {
  opacity: 0;
}
.apps .donwload a div {
  width: 110px;
  height: 33px;
  line-height: 30px;
  border: 1px solid #fff;
  border-radius: 4px;
  box-sizing: border-box;
  font-size: 15px;
  text-align: center;
  color: #fff;
}
.apps .donwload .downhov {
  opacity: 0;
}
.apps .donwload img {
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  transition-duration: 0.3s;
}
